const Inicio = ({setState}) => {
    const planetas = ["Sol", "Mercurio", "Venus", "Tierra", "Marte", "Jpiter", "Saturno", "Urano", "Neptuno"];

    return (
        <div id="Inicio">
            {planetas.map((planeta, index) =>
                <div id={planeta} key={index} className="div-img width" onClick={()=>setState(planeta)}>
                    <img src={`imagenes/${planeta}.png`} alt={planeta} title={planeta}/>
                </div>
            )}
        </div>
    )
}

export default Inicio;